@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,700;1,400;1,700&display=swap);
html {
    box-sizing: border-box;
    -webkit-font-smoothing: antialiased;
}

*,
::after,
::before {
    box-sizing: inherit;
    -webkit-font-smoothing: inherit;
}

body {
    background-color: #e8e8ee;
    font-family: 'Montserrat', sans-serif;
    margin: 0;
}

input {
    font-family: 'Montserrat', sans-serif;
}
.form {
    display: grid;
    grid-template-columns: 1fr auto;
    grid-gap: 20px;
}

.form__input {
    padding: 10px;
    border: 1px solid #ddd;
}

.form__button {
    border:none;
    background-color: #008080;
    color: #fff;
    padding: 10px;
    transition: 0.3s;
}

.form__button:hover {
    background-color: rgb(0, 153, 153);
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.form__button:active {
    background-color: rgb(0, 179, 179);
}

@media (max-width: 767px) {
    .form {
        grid-template-columns: 1fr;
    }
}
.tasks {
    list-style: none;
    margin: 0;
    padding: 0;
}

.tasks__item {
    display: grid;
    grid-template-columns: auto 1fr auto;
    grid-gap: 10px;
    align-items: center;
    padding: 10px;
    border-bottom: 1px solid #ddd;
}

.tasks__item--hidden {
    display: none;
}

.tasks__button {
    border: 0;
    width: 30px;
    height: 30px;
    color: #fff;
    padding: 0;
    transition: 0.3s;
    cursor: pointer;
}

.tasks__button--toggleDone {
    background-color: hsl(120, 61%, 34%);
}

.tasks__button--toggleDone:hover {
    background-color: hsl(120, 61%, 39%);
}

.tasks__button--toggleDone:active {
    background-color: hsl(120, 61%, 39%);
}

.tasks__button--remove {
    background-color: hsl(348, 83%, 47%);
}

.tasks__button--remove:hover {
    background-color: hsl(348, 83%, 52%);
}

.tasks__button--remove:active {
    background-color: hsl(348, 83%, 57%);
}

.tasks__content {
    color: #000;
    transition: 0.5s;
}

.tasks__content--done {
    text-decoration: line-through;
    color: #999;
}
.buttons {
display: flex;
flex-wrap: wrap;
}

.buttons__button {
background: transparent;
color: teal;
border: none;
margin: 0 0 0 20px;
transition: color 0.3s;
}

@media(max-width: 767px) {
    .buttons__button {
        flex-basis: 100%;
    }
}

.buttons__button:hover {
    color: hsl(180, 100%, 35%);
}

.buttons__button:disabled {
    color: #ccc;
}


.section {
    margin: 10px 0;
    background: white;
    box-shadow: 0 0 5px #ddd;
    }
    
    .section__header {
        border-bottom: 1px solid #ddd;
        display: grid;
        grid-template-columns: auto auto;
        grid-gap: 20px;
        justify-content: space-between;
        align-items: center;
        padding: 20px;
    }
    
    @media(max-width: 767px) {
        .section__header {
            grid-template-columns: 1fr;
        }
    }
    
    .section__title {
        font-size: 20px;
        margin: 0;
    }
    
    .section__body {
        padding: 20px;
    }
.container {
    max-width: 900px;
    padding: 20px;
    margin: 0 auto;
}
